<template>
  <el-card>
    <template #header>
      <span>Employment Types</span>
      <el-button type="primary" plain @click="dialog = true"
        >Add New Employment Type</el-button
      >
    </template>
    <el-dialog v-model="dialog" title="Add Employment Types">
      <el-form
        :model="employment_type"
        size="large"
        ref="employment_type"
        label-position="top"
        :inline="true"
      >
        <el-row>
          <el-col :span="12">
            <el-form-item
              label="Employment type"
              prop="employment_type"
              :rules="[
                {
                  required: true,
                  message: 'Employment type is required',
                  trigger: ['blur', 'change'],
                },
              ]"
            >
              <el-input
                v-model="employment_type.employment_type"
                placeholder="Enter employment type"
              /> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="Pensionable">
              <el-select
                v-model="employment_type.pensionable"
                filterable
                placeholder="Select pensionable"
              >
                <el-option label="Yes" :value="true" />
                <el-option label="No" :value="false" />
              </el-select> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="Deduct NHIF">
              <el-select
                v-model="employment_type.deduct_nhif"
                filterable
                placeholder="Select deduct nhif"
              >
                <el-option label="Yes" :value="true" />
                <el-option label="No" :value="false" />
              </el-select> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="Deduct NSSF">
              <el-select
                v-model="employment_type.deduct_nssf"
                filterable
                placeholder="Select deduct nssf"
              >
                <el-option label="Yes" :value="true" />
                <el-option label="No" :value="false" />
              </el-select> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="Deduct Tax">
              <el-select
                v-model="employment_type.deduct_tax"
                filterable
                placeholder="Select deduct tax"
              >
                <el-option label="Yes" :value="true" />
                <el-option label="No" :value="false" />
              </el-select> </el-form-item
          ></el-col>
          <el-col :span="12">
            <el-form-item label="Deduct Tax">
              <el-select
                v-model="employment_type.tax_type"
                filterable
                placeholder="Select tax type"
              >
                <el-option label="PAYE" value="Paye" />
                <el-option label="WITHHOLDING" value="Withholding" />
              </el-select> </el-form-item
          ></el-col>
        </el-row>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialog = false">Cancel</el-button>
          <el-button
            type="primary"
            plain
            @click="submit('employment_type')"
            :loading="loading"
            >Save</el-button
          >
        </span>
      </template>
    </el-dialog>

    <el-table ref="tableRef" :data="employment_types" v-loading="loading">
      <el-table-column prop="employment_type" label="Employment Type" />
      <el-table-column
        prop="pensionable"
        label="Pensionable"
        :formatter="$helpers.formatBoolean"
      />
      <el-table-column
        prop="deduct_tax"
        label="Deduct Tax"
        :formatter="$helpers.formatBoolean"
      />
      <el-table-column prop="tax_type" label="Tax Type" />
      <el-table-column
        prop="deduct_nhif"
        label="Deduct NHIF"
        :formatter="$helpers.formatBoolean"
      />
      <el-table-column
        prop="deduct_nssf"
        label="Deduct NSSF"
        :formatter="$helpers.formatBoolean"
      />

      <el-table-column prop="action" label="Action" width="70">
        <template #default="props">
          <el-dropdown>
            <span class="el-dropdown-link">
              <i class="fas fa-ellipsis-v"></i>
            </span>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="edit(props.row)"
                  >Edit</el-dropdown-item
                >
                <el-dropdown-item @click="deleteEmploymentType(props.row.id)"
                  >Delete</el-dropdown-item
                >
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="employment_types ? employment_types.length : 0"
    />
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      loading: false,
      employment_type: {
        pensionable: false,
        deduct_tax: true,
        tax_type: "Paye",
        deduct_nhif: true,
        deduct_nssf: true,
      },
    };
  },
  mounted() {
    this.$store.dispatch("company/getEmploymentTypes");
  },
  computed: {
    employment_types() {
      this.loading = true;
      if (this.$store.state.company.employment_types != undefined) {
        this.loading = false;
        return this.$store.state.company.employment_types;
      }
    },
  },
  methods: {
    edit(data) {
      this.employment_type = data;
      this.dialog = true;
    },
    submit(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$store
            .dispatch("company/saveEmploymentType", this.employment_type)
            .then(
              (res) => {
                this.employment_type = {};
                this.$store.dispatch("company/getEmploymentTypes");
                this.dialog = false;
                this.loading = false;
                this.$notify({
                  title: "Success",
                  message: res.data.message,
                  type: "success",
                });
              },
              (error) => {
                this.loading = false;
                this.message =
                  (error.response &&
                    error.response.data &&
                    error.response.data.message) ||
                  error.message ||
                  error.toString();
                this.$notify({
                  title: "Error",
                  message: this.message,
                  type: "error",
                });
              }
            );
        } else {
          return false;
        }
      });
    },
    deleteEmploymentType(id) {
      if (confirm("Do you really want to delete this record?")) {
        this.$store
          .dispatch("company/deleteEmploymentType", id)
          .then((response) => {
            this.$store.dispatch("company/getEmploymentTypes");
            this.$notify({
              title: "Success",
              message: response.data.message,
              type: "success",
            });
          })
          .catch((error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();

            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          });
      }
    },
  },
};
</script>